*{
    padding:0px;
    margin:0px;
}
.container{
    position: fixed;
    top: 0px;
    background-color: brown;
    padding: 36px 0px;
    z-index: -1;
    width: 100%;
    
    
}
.container h1{
    color: white;
    font-weight: bold;
    margin: -9px 29px;
    font-size: 40px;
}
.image{
    width: 100%;
    height: 194px
    




}
.image img{
    position: absolute;
    width: 100%;
    top: 0px;
    z-index: -2;
    height: 350px;
    object-fit: cover;
    
}